import React, { useState, useEffect } from "react";
import { Helmet } from "rl-react-helmet";
import styled from "styled-components";
import axiosInstance from "../services/axiosApi";

import Treemap from './Treemap';

const Wrapper = styled.div`
    display: flex;
    flex-direction: column;
    width: 100vw;
    min-height: 80vh;
    background-color: var(--main-bg-color);
    align-items: center;
`;


const TopMenu = styled.div`
    display: flex;
    height: 2rem;
    width: 100vw;
    background-color: var(--main-dark-gray);
    align-items: center;
`

const MenuList = styled.ul` 
    display: flex;
    height: 2rem;
    width: 20rem;
    background-color: var(--main-dark-gray);
`

const LowerMenuItem = styled.li`
    list-style : none;
    color: white;
    font-size: 0.8rem;
    font-weight: bold;
    background-color: var(--main-dark-gray);

    width: 4rem;
    justify-content: space-between;

    padding-left: 2rem;
    padding-top: 0.5rem;
`

export default () => {
    const [market, setMarket] = useState("KOSPI");
    const [performance, setPerformance] = useState("1D")
    const [data, setData] = useState(null);
    //const data = require("../static/json/treemapSample")

    useEffect(() => {
        async function getMapData() {
            try {
                let response = await axiosInstance.get(`/mapdata/${performance}/`);
                console.log("@@@@@@@@@@@@ mapdata", response.data)
                setData(response.data);
            } catch (error) {
                const _content =
                    (error.response &&
                        error.response.data &&
                        error.response.data.message) ||
                    error.message ||
                    error.toString();
                //setError(_content);
                console.log("ERROR:", _content)
            }
        }
        getMapData();
    }, [market, performance])

    if (data === null)
        return <h1>Loading...</h1>

    return (
        <Wrapper>
            <Helmet>
                <title>Map</title>
            </Helmet>
            <TopMenu>
                <MenuList>
                    <LowerMenuItem onClick={() => setMarket("KOSPI")}>KOSPI</LowerMenuItem>
                    <LowerMenuItem onClick={() => setMarket("ETF")}>ETF</LowerMenuItem>
                </MenuList>
                <select name="performance" id="performance" onChange={(e) => setPerformance(e.target.value)}>
                    <option value="1D">1 Day performance</option>
                    <option value="7D">7 Days performance</option>
                    <option value="1M">1 Montn performance</option>
                    <option value="1Y">1 Year performance</option>
                </select>

            </TopMenu>
            <div className="App">
                <h2>Graphs with React</h2>
                <Treemap width={800} height={400} data={data} />
            </div>
        </Wrapper>
    );
};
