import * as d3 from 'd3';
import React, { useRef, useEffect } from 'react';

function Treemap({ width, height, data }) {
    const ref = useRef();

    useEffect(() => {
        const svg = d3.select(ref.current)
            .attr("width", width)
            .attr("height", height)
            .style("border", "1px solid black")
    }, []);

    useEffect(() => {
        draw();
    }, [data]);

    function approximateColor1ToColor2ByPercent(color1, color2, percent) {
        var red1 = parseInt(color1[1] + color1[2], 16);
        var green1 = parseInt(color1[3] + color1[4], 16);
        var blue1 = parseInt(color1[5] + color1[6], 16);

        var red2 = parseInt(color2[1] + color2[2], 16);
        var green2 = parseInt(color2[3] + color2[4], 16);
        var blue2 = parseInt(color2[5] + color2[6], 16);

        var red = Math.round(mix(red1, red2, percent));
        var green = Math.round(mix(green1, green2, percent));
        var blue = Math.round(mix(blue1, blue2, percent));

        return generateHex(red, green, blue);
    }

    function generateHex(r, g, b) {
        r = r.toString(16);
        g = g.toString(16);
        b = b.toString(16);

        // to address problem mentioned by Alexis Wilke:
        while (r.length < 2) { r = "0" + r; }
        while (g.length < 2) { g = "0" + g; }
        while (b.length < 2) { b = "0" + b; }

        return "#" + r + g + b;
    }

    function mix(start, end, percent) {
        return start + ((percent) * (end - start));
    }


    const draw = () => {
        const svg = d3.select(ref.current);

        // Give the data to this cluster layout:
        var root = d3.hierarchy(data).sum(function (d) { return d.marketCap });

        // initialize treemap
        d3.treemap()
            .size([width, height])
            .paddingTop(28)
            .paddingRight(7)
            .paddingInner(3)
            (root);

        const color = d3.scaleOrdinal()
            .domain(["TECHNOLOGY"])
            .range(["#367701"]);

        const gradiation = d3.scaleLinear()
            .domain([-3, 3])
            .range([0, 1]);

        // Select the nodes
        var nodes = svg
            .selectAll("rect")
            .data(root.leaves())

        // draw rectangles
        nodes
            .enter()
            .append("rect")
            .attr('x', function (d) { return d.x0; })
            .attr('y', function (d) { return d.y0; })
            .attr('width', function (d) { return d.x1 - d.x0; })
            .attr('height', function (d) { return d.y1 - d.y0; })
            .style("stroke", "black")
            //.style("fill", function (d) { return color(d.parent.data.ticker) })
            .style("fill", function (d) {
                return approximateColor1ToColor2ByPercent("#FF0000", "#00FF00", gradiation(d.data.diff))
            })

        nodes.exit().remove()

        // select node titles
        var nodeText = svg
            .selectAll("text")
            .data(root.leaves())

        // add the text
        nodeText.enter()
            .append("text")
            .attr("x", function (d) { return d.x0 + 5 })
            .attr("y", function (d) { return d.y0 + 20 })
            .text(function (d) { return d.data.ticker })
            .attr("font-size", "0.7rem")
            .attr("font-weight", "bold")
            .attr("fill", "white")

        nodeText.exit().remove()


        //select node values
        var nodeVals = svg
            .selectAll("vals")
            .data(root.leaves())

        nodeVals
            .transition().duration(300)
            .attr("x", function (d) { return d.x0 + 5 })
            .attr("y", function (d) { return d.y0 + 35 })
            .text(function (d) { return d.data.diff })

        // add the values
        nodeVals.enter()
            .append("text")
            .attr("x", function (d) { return d.x0 + 5 })
            .attr("y", function (d) { return d.y0 + 35 })
            .text(function (d) { return d.data.diff })
            .attr("font-size", "11px")
            .attr("fill", "white")

        nodeVals.exit().remove()

        // add the parent node titles
        svg
            .selectAll("titles")
            .data(root.descendants().filter(function (d) { return d.depth == 1 }))
            .enter()
            .append("text")
            .attr("x", function (d) { return d.x0 })
            .attr("y", function (d) { return d.y0 + 21 })
            .text(function (d) { return d.data.ticker })
            .attr("font-size", "19px")
            .attr("fill", function (d) { return color(d.data.ticker) })

        // // Add the chart heading
        // svg
        //     .append("text")
        //     .attr("x", 0)
        //     .attr("y", 14)    // +20 to adjust position (lower)
        //     .text("Three group leaders and 14 employees")
        //     .attr("font-size", "19px")
        //     .attr("fill", "grey")
    }


    return (
        <div className="chart">
            <svg ref={ref}>
            </svg>
        </div>

    )

}

export default Treemap;